import {Container, Row, Col} from 'react-bootstrap';

function Footer() {
  return (
    <div style={{background: '#3a3a3a', color: 'white'}}>
        <Container fluid className='pt-4'>
            <Row>
                <Col sm={12} md={3}>
                    <h5>About InCredit</h5><br/>
                    <p><span>InCredit is a finance platform which provide loans &amp; cards at your doorsteps.</span></p>
                </Col>
                <Col sm={12} md={3}>
                    <h5>Products</h5><br/>
                    <span>Personal Loans</span><br/>
                    <span>Car Loans</span>
                </Col>
                <Col sm={12} md={3}>
                    <h5>Contact Us</h5><br/>
                    <span>95155 88746</span><br/>
                    <span>info@incredit.in</span>
                </Col>
                <Col sm={12} md={3}>
                    <h5>Follow Us</h5><br/>
                    <span>Facebook</span><br/>
                    <span>Instagram</span><br/>
                    <span>Linkedin</span><br/>
                    <span>Youtube</span>
                </Col>
            </Row>
            <Row>
                <Col sm={12}>2022 © InCredit</Col>
            </Row>
        </Container>
    </div>
  );
}

export default Footer;
