import { useState, useEffect } from 'react';
import {Nav, Navbar, NavDropdown, Container, Offcanvas, Collapse } from 'react-bootstrap';
import { NavLink } from "react-router-dom";
import "./Header.scss";
//import { getApplicationBanner } from '../../Util/utilHelper';
//const getApplicationBanner:any = lazy(() => import("../../Util/utilHelper") as any);

function Header() {
    const [headerMenus, setHeaderMenus]:any = useState([]);
    const [show, setShow] = useState(false);
    const [showOffCanvas, setShowOffCanvas] = useState(false);
    const [routePath, setRoutePath] = useState("");
    const [showSubMenu, setShowSubMenu] = useState(false);
    let timer;

    const handleClose = () => setShowOffCanvas(false);
    const handleShow = () => setShowOffCanvas(!showOffCanvas);
    const openMenu = event => {
        clearTimeout(timer);
        event.preventDefault();
        setShow(true);
    };
    
    const routerChange = ( selectedPath ) => {
        setRoutePath(selectedPath);
    };

    const menuClickHandler = (event, value ) => {
        console.log(value, event);
        if( value.id === "productsMain" ) {
            setShowSubMenu( !showSubMenu );
        } else {
            handleClose();
        }
    }
      
      const closeMenu = event => {
        event.preventDefault();
        timer = setTimeout(() => {
            setShow(false);
        }, 200);
      };

    useEffect(() => {
        if( headerMenus.length === 0 ) {
            import("../../Util/utilHelper").then((res:any) => {
                res.getApplicationBanner().then((res:any) => {
                    setHeaderMenus( res.modelMap.ApplicationBannerModel.mainMenuList );
                });
            });
        }
    });
  return (
    <div>
        <Navbar fixed='top'>
            <Container fluid>
                <Navbar.Brand href="#">
                <img
                    src={require("../../assets/images/incredit_logo.png")}
                    width="35"
                    height="35"
                    className="d-inline-block align-top"
                    alt="Incredit logo"/>
                    {' '}<span className='logo-text'>InCredit</span>
                </Navbar.Brand>
                <button type="button" className={showOffCanvas?"d-block d-sm-none navbar-toggle":"d-block d-sm-none navbar-toggle collapsed"} onClick={handleShow}>
                   <span className="icon-bar top-bar"></span>
                   <span className="icon-bar middle-bar"></span>
                   <span className="icon-bar bottom-bar"></span>
               </button>
                 <Nav className='max-width-575 main-menus'
                    style={{ maxHeight: '100px' }}
                    navbarScroll>
                    { headerMenus.map( (value:any, index:number) => (
                        ( (value.subMenu === null && <NavLink onClick={() => routerChange(value.path)} className="nav-link" to={value.path} key={value.id}>{value.titleMenu}</NavLink>) ||
                        ( value.subMenu !== null && <NavDropdown show={show} className={routePath === "products" || routePath === "personalLoan" || routePath === "carLoan" ? "active": ""}
                            onMouseEnter={openMenu} 
                            onMouseLeave={closeMenu} title={value.titleMenu} id={value.id} key={value.id}>
                            { value.subMenu.map( (value) => (
                                <NavLink onClick={() => routerChange(value.path)} className="nav-link" id={value.id} to={value.path} key={value.id}>{value.titleMenu}</NavLink>
                            ) ) }
                        </NavDropdown> ))
                    )) }
                </Nav>
                <Offcanvas onHide={handleClose} backdrop={true} 
                id="offcanvasNavbar" className="d-block d-sm-none"
                aria-labelledby="offcanvasNavbarLabel"
                show={showOffCanvas} 
                >
                    <Offcanvas.Body>
                        <Nav className="justify-content-end flex-grow-1 pe-3">
                            {
                                headerMenus.map((value, index) => (
                                    <li key={value.id}><NavLink onClick={(event) => menuClickHandler(event, value) } className="nav-link" to={value.path}>{value.titleMenu}{ value.subMenu !== null && <span className={showSubMenu?'submenu-icon rotation': 'submenu-icon '}>v</span>}</NavLink>
                                    {value.subMenu !== null && 
                                    <Collapse in={showSubMenu}>
                                    <ul>
                                        {
                                            value.subMenu.map( value => (
                                                <li className="nav-link" key={value.id}><NavLink onClick={(event) => menuClickHandler(event, value) } className="nav-link" to={value.path}>{value.titleMenu}</NavLink></li>
                                            ) )
                                        }
                                    </ul>
                                    </Collapse>}
                                    </li>
                                ))
                            }
                        </Nav>
                    </Offcanvas.Body>
                </Offcanvas>
            </Container>
        </Navbar>
        {/* <Navbar bg="light" fixed='top' expand="lg">
            <Container fluid>
                
                <Navbar.Brand href="#">Navbar Offcanvas</Navbar.Brand>
                <button className='d-none d-sm-block' onClick={handleShow}>Test</button>
                <Offcanvas onHide={handleClose}
                id="offcanvasNavbar"
                aria-labelledby="offcanvasNavbarLabel"
                show={showOffCanvas}
                >
                    <Offcanvas.Header closeButton>
                        <Offcanvas.Title id="offcanvasNavbarLabel">Offcanvas</Offcanvas.Title>
                    </Offcanvas.Header>
                    <Offcanvas.Body>
                        <Nav className="justify-content-end flex-grow-1 pe-3">
                        <Nav.Link href="#action1">Home</Nav.Link>
                        <Nav.Link href="#action2">Link</Nav.Link>
                        <NavDropdown title="Dropdown" id="offcanvasNavbarDropdown">
                            <NavDropdown.Item href="#action3">Action</NavDropdown.Item>
                            <NavDropdown.Item href="#action4">Another action</NavDropdown.Item>
                            <NavDropdown.Divider />
                            <NavDropdown.Item href="#action5">
                            Something else here
                            </NavDropdown.Item>
                        </NavDropdown>
                        </Nav>
                    </Offcanvas.Body>
                </Offcanvas>
            </Container>
            </Navbar> */}
    </div>
  );
}

export default Header;
